body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
    display: flex;
    justify-content: space-between;
}

.nav .right-container {
    display: flex;
}

.nav .right-container a {
    text-decoration: none;
    color: black;
}

.nav-links {
    padding: 6vh;
}

.nav-links:hover {
    background-color: #E8EDE6;
}

.nav-links:target {
    background-color: #E8EDE6;
}

.active {
    background-color: #E8EDE6;
}

.nav h1 {
    margin: 2.5vh 0;
    font-family: 'Hepta Slab', serif;
}

.nav img {
    width: 4.5vw;
}

.nav .left-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav a {
    text-decoration: none;
    color: black;
    padding: 2vh;
}

.home .top-container {
    height: 85vh;
    background-color: #E8EDE6;
    display: flex;
    justify-content: flex-end;
}

.home img {
    margin: 45vh 0 0 2vw;
}

.home .right-container {
    padding: 5vh 0 0 5vw;
    width: 65vw;
}

.p1 {
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    margin: 3vh;
}

.p2 {
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    padding-left: 30%;
    margin: 3vh;
}

.p3 {
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    padding-left: 60%;
    margin: 3vh 3vh 0 3vh;
}

.presentation-text {
    padding: 5vh 0 0 5vw;
    width: 70%;
    text-align: justify;
    margin: 0;
    letter-spacing: 1px;
    line-height: 1.5em;
}

.home button {
    border-radius: 10px;
    border: 1px solid black;
    padding: 1em;
    font-size: 1em;
    background-color: #E8EDE6;
    outline: 0;
    margin: 5% 10% 5% 20%;
}

.home button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}
.products {
    display: flex;
    justify-content: center;
    align-items: center;
}

.products-top-container {
    height: 15vh;
    background-color: #E8EDE6;
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 1px;
}

.all-products {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70%;
}


.productItem {
    width:33%;
    height: 40vh;
    text-align: center;
}

.productItem p {
    font-family: 'Hepta Slab', serif;
    font-size: 1.2em;
    font-weight: bold;
}

.productItem img {
    height: 45%;
    padding: 5%;
}

.productItem a {
    text-decoration: none;
    color: #6e2142;
}
.product-card {
    width:30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 1vh;
    font-family: 'Hepta Slab', serif;
}

.product-card .active {
    background-color: #d2c8c8;
    border-radius: 50%;
    border : 2px solid #d2c8c8;
    outline: 0;
}

.product-card .not-active {
    background-color: white;
    border-radius: 50%;
    border : 2px solid #d2c8c8;
    outline: 0;
}

.product-card img {
    width: 3vw;
    padding: 1vw;
}

.product-card p {
    padding: 0.8%;
}
.ma-cagette .top-container {
    height: 15vh;
    background-color: #E8EDE6;
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 1px;
}

.ma-cagette .central-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.ma-cagette .left-container {
    width: 60%;
    height: 100%;
}

.ma-cagette .cagette {
    padding: 3%;
    display: flex;
    justify-content: center;
    align-items:center;
    flex-wrap: wrap;
    height: 65%;
}

.ma-cagette .right-container {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ma-cagette .ticket {
    border-radius: 25px;
    border: 2px solid #d2c8c8;
    height: 55vh;
    width: 80%;
    padding: 3vh;
    position: relative;
}

.ma-cagette .total {
    position: absolute;
    top: 45vh;
    font-size: 1.5em;
    color:#6e2142;
}

.ma-cagette .totalPrice {
    color: black;
    font-size: 1em;
    position: absolute;
    left: 30vw;
    bottom: 6.5vh;
}

.ma-cagette .button {
    border-radius: 10px;
    border: 1px solid #6e2142;
    padding: 0.6em;
    font-size: 0.6em;
    outline: 0;
    background-color: white;
    margin-top: 1em;
}

.ma-cagette .button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}




.ticketLine {
    line-height: 2em;
}

.ticketLine .bullet {
    color:#d2c8c8;
    padding: 0.2em;
    font-weight: bolder
}

.ticketLine .quantity {
    color: grey;
    font-size: 0.9em;
}
.signup {
    background-color: #E8EDE6;
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 5vh;
    position: relative;
    text-align: center;
    line-height: 1.5em;
}

.signup h1{
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    margin: 2vh 0 5vh 0;
}

.signup input{
    width:100%;
}

.signup input[type=text],
.signup input[type=password],
.signup input[type=tel],
.signup input[type=email] {
    width: 100%;
    padding: 10px 20px;
    margin: 8px 0;
    box-sizing: border-box;
  }

.signup img {
    width: 35vw;
    position: absolute;
    top: 30vh;
    left: 64vw;
}

.signup .input {
    text-align: center;
}

.signup textarea:focus, input:focus{
    outline: none;
    border: 2px solid #6e2142;
}

.signup .button {
    border-radius: 10px;
    border: 1px solid black;
    padding: 1em;
    font-size: 1em;
    background-color: #E8EDE6;
    outline: 0;
    width: 50%;
    margin: 5% 0;
}

.signup .button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}
.login {
    background-color: #E8EDE6;
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 5vh;
    position: relative;
    text-align: center;
    line-height: 1.5em;
}

.login h1{
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    margin: 5vh;
}

.login input[type=email],
input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
  }

.login img {
    width: 35vw;
    position: absolute;
    top: 30vh;
    right: 65vw;
}

.login .input {
    text-align: center;
}

.login input{
    width:100%;
}

.login textarea:focus, input:focus{
    outline: none;
    border: 2px solid #6e2142;
}

.login .button {
    border-radius: 10px;
    border: 1px solid black;
    padding: 1em;
    font-size: 1em;
    background-color: #E8EDE6;
    outline: 0;
    width: 50%;
    margin: 10% 0;
}

.login .button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}

.login a {
    text-decoration: none;
    color: #6e2142;
}
.confirmation .top-container {
    height: 15vh;
    background-color: #E8EDE6;
    display: flex;
    justify-content: center;
    align-items: center;
}

.confirmation h1{
    font-family: 'Hepta Slab', serif;
    font-size: 2em;
    margin: 5vh;
}

.confirmation h2{
    font-family: 'Hepta Slab', serif;
    margin: 2vh 0 0 0;
}

.confirmation .main-container {
    display: flex;
    justify-content: flex-start;
    height: 70vh;
}

.confirmation .left-container {
    width: 30%;
    padding: 0 2%;
}

.middle-container {
    width: 40%;
}

.confirmation .right-container {
    width: 30%;
    background-color: #d2c8c8;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between
}

.confirmation .right-container .total {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.confirmation .right-container .total p {
    font-size: 1.3em;
}

.confirmation .right-container button {
    border-radius: 10px;
    border: 1px solid black;
    padding: 0.5em;
    font-size: 1em;
    background-color: #d2c8c8;
    outline: 0;
    width: 90%;
    margin-top: 5%;
}

.confirmation .right-container button:hover {
    background-color: #E8EDE6;
}

.confirmation .right-container a {
    padding: 2vh;
    text-decoration: none;
    color: #6e2142;
}

.confirmation .bottom-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.address-form {
    width: 20vw;
    border-radius: 25px;
    border: 2px solid #d2c8c8;
    margin: 5vh 2vw 0 2vw;
    display: flex;
    padding: 3vh;
    position: relative;
    text-align: center;
    line-height: 1.5em;
}

.address-form input[type=text] {
    width: 60%;
    padding: 5px 10px;
    margin: 8px 0;
    box-sizing: border-box;
}

.address-form .button {
    border-radius: 10px;
    border: 1.5px solid #d2c8c8;
    padding: 1em;
    font-size: 1em;
    background-color: white;
    outline: 0;
    width: 30%;
    margin-top: 5%;
}

.address-form .button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}

.address-form .input {
    display: flex;
    justify-content: space-between;
}

.address-form .input label {
    padding-top: 0.5em;
}
.addressCard {
    border: 1px solid black;
    width: 50%;
    padding: 4%;
    border-radius: 25px;
    border: 2px solid #d2c8c8;
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
}

.addressCard button {
    border-radius: 10px;
    border: 1.5px solid #d2c8c8;
    padding: 0.5em;
    font-size: 1em;
    background-color: white;
    outline: 0;
    width: 30%;
    margin-top: 5%;
}

.addressCard button:hover {
    background-color: #d2c8c8;
    border: 1.5px solid #d2c8c8;
}

.confirmed-order .top-container {
    height: 15vh;
    background-color: #E8EDE6;
    display: flex;
    justify-content: center;
    align-items: center;
}

.confirmed-order h1{
    font-family: 'Hepta Slab', serif;
    font-size: 2em;
    margin: 5vh;
}

.confirmed-order h2{
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    margin: 20vh;
}

.confirmed-order .main-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    width: 100%;
    position: relative;
}

.confirmed-order .central-container {
    background-color: #E8EDE6;
    height: 80%;
    width:70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.confirmed-order img {
    width: 20vw;
    position: absolute;
    top: 40vh;
    left: 75vw;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

