@import url('https://fonts.googleapis.com/css?family=Hepta+Slab:300&display=swap');

.signup {
    background-color: #E8EDE6;
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 5vh;
    position: relative;
    text-align: center;
    line-height: 1.5em;
}

.signup h1{
    font-family: 'Hepta Slab', serif;
    font-size: 3em;
    margin: 2vh 0 5vh 0;
}

.signup input{
    width:100%;
}

.signup input[type=text],
.signup input[type=password],
.signup input[type=tel],
.signup input[type=email] {
    width: 100%;
    padding: 10px 20px;
    margin: 8px 0;
    box-sizing: border-box;
  }

.signup img {
    width: 35vw;
    position: absolute;
    top: 30vh;
    left: 64vw;
}

.signup .input {
    text-align: center;
}

.signup textarea:focus, input:focus{
    outline: none;
    border: 2px solid #6e2142;
}

.signup .button {
    border-radius: 10px;
    border: 1px solid black;
    padding: 1em;
    font-size: 1em;
    background-color: #E8EDE6;
    outline: 0;
    width: 50%;
    margin: 5% 0;
}

.signup .button:hover {
    background-color: #d2c8c8;
    border: 1px solid #d2c8c8;
}